<template>
  <div class="app">
    <h1>Posts</h1>
    <my-input v-model:value="searchQuery" placeholder="Search..." />
    <div class="app__btns">
      <my-button @click="showDialog" class="create-post">Create Post</my-button>
      <my-select class="select" v-model="selectedSort" :options="sortOptions" />
    </div>
    <transition name="modal-show">
      <my-dialog v-model:show="dialogVisible" style="will-change: transform">
        <post-form @create="createPost" />
      </my-dialog>
    </transition>
    <transition>
      <post-list :posts="searchedPosts" @remove="removePost" v-if="!isPostLoading" style="will-change: transform" />
      <div v-else style="will-change: transform">Posts loading...</div>
    </transition>
    <div class="page__wrapper">
      <post-pages :pages="totalPages" :page="page" @change="changePage" />
    </div>
  </div>
</template>

<script>
import postForm from "./components/postForm.vue";
import postList from "./components/postList.vue";
import postPages from "./components/postPages.vue";
import axios from "axios"

export default {
  components: {
    postList,
    postForm,
    postPages
  },
  data() {
    return {
      posts: [],
      dialogVisible: false,
      isPostLoading: false,
      selectedSort: "",
      searchQuery: "",
      page: 1,
      limit: 10,
      totalPages: 0,
      sortOptions: [
        { value: "title", name: "Title" },
        { value: "body", name: "Description" },
      ],
    };
  },
  methods: {
    createPost(post) {
      this.posts.unshift(post);
      this.dialogVisible = false;
    },
    removePost(post) {
      this.posts = this.posts.filter((p) => p.id != post.id);
    },
    showDialog() {
      this.dialogVisible = true;
    },
    changePage(pageNumber) {
      this.page = pageNumber;
    },
    async fetchPosts() {
      try {
        this.isPostLoading = true;

        const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {
          params: {
            _page: this.page,
            _limit: this.limit,
          }
        });

        this.totalPages = Math.ceil(response.headers['x-total-count'] / this.limit);
        this.posts = await response.data;
      } catch (error) {
        alert("Sever Error");
      } finally {
        setTimeout(() => (this.isPostLoading = false), 1000);
      }
    },
  },
  mounted() {
    this.fetchPosts();
  },
  computed: {
    sortedPosts() {
      return [...this.posts].sort((post1, post2) =>
        post1[this.selectedSort]?.localeCompare(post2[this.selectedSort])
      );
    },
    searchedPosts() {
      return this.sortedPosts.filter((post) => post.title.toLowerCase().includes(this.searchQuery.toLowerCase()));
    },
  },
  watch: {
    page() {
      this.fetchPosts()
    }
  },
};
</script>

